import React, { useState } from "react";
import './app1.css';
import 'bootstrap/dist/css/bootstrap.min.css'; // Bootstrap styles
import '@fortawesome/fontawesome-free/css/all.min.css'; // Font Awesome icons

// import { Image2 } from '../images/name.png';
import Image2 from '../images/name.png';


 
const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
 
  const handleLogin = (event) => {
    event.preventDefault(); // Prevent the default form submission behavior
 
    // Hardcoded credentials for simulation
    const hardcodedUsername = "admin";
    const hardcodedPassword = "123456";
 
    // Check if the entered credentials match the hardcoded values
    if (username === hardcodedUsername && password === hardcodedPassword) {
      // Simulate a successful login
      console.log("Login successful");
 
      // Redirect to the dashboard or perform other actions
      window.location.href = "/dashboard";
      setUsername("");
      setPassword("");
    } else {
      // Display a popup for invalid login
      alert("Invalid login. Please try again.");
    }
  };
 
  return (
<div className="container text-start">
<div className="login-container">
<form onSubmit={handleLogin} className="justify-content-space-between">
<div className="text-center mb-4 ml-5">
{/* <img src="" alt="Logo" style={{ width: "500px" }} /> */}
{/* <img src={{Image2}} /> */}
<img src={Image2} alt="Description" style={{ width: "500px" }}/>
</div>
<div className="text-light text-center mb-4 ml-5">
<h1 style={{ fontWeight: "bold", fontSize: "60px" }}>LOG IN</h1>
</div>
<div className="form-group p-2 ml-5">
<input
              type="text"
              className="form-control"
              id="username"
              placeholder="Username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
</div>
<div className="form-group p-2 ml-5">
<input
              type="password"
              className="form-control"
              id="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
</div>
<div className="text-center mt-4 p-2 ml-5">
<button className="btn btn-block" style={{ backgroundColor: "rgb(255, 157, 0)", color: "white" }} type="submit">
              Sign In
</button>
</div>
</form>
</div>
</div>
  );
};
 
export default Login;