//         setFraudRecoveryRateData(response.data.Fraud_Recovery_Rate[0] || {});
//         setFraudSeverityData(response.data.Fraud_Severity_by_Monetary_Loss || []);
//         setFraudCasesByLocationData(response.data.Fraud_Cases_by_Geographical_Location || []);
//         setFalsePositiveRateData(response.data.False_Positive_Rate_Over_Time || []); // New false positive rate data

