import React from 'react';
import BankDetails from './BankDetails'; 
import { useNavigate } from 'react-router-dom'; 
import './Sidebar.css';

import Image from '../images/name.png';

const Sidebar = () => {
  const navigate = useNavigate(); 
  const handleLogout = () => {
    navigate('/'); 
  };

  return (
    <div className="wrapper">
      <aside id="sidebar" className="expand"> 
        <div className="sidebar-logo">
          <img src={Image} style={{ width: "190px", height: "40px" }} alt="Logo" />
        </div>

        <ul className="sidebar-nav">
          <li className="sidebar-item">
            <a href="#" className="sidebar-link">
              <i className="fas fa-university"></i>
              <span>Fraud Analytics Dashboard</span>
            </a>
          </li>
        </ul>

        <div className="sidebar-footer">
          <a href="#" className="sidebar-link" onClick={handleLogout}>
            <i className="fas fa-sign-out-alt"></i>
            <span>Logout</span>
          </a>
        </div>
      </aside>

      <div className="main">
        <BankDetails /> 
      </div>
    </div>
  );
};

export default Sidebar;
