import React, { useState } from 'react';
import { Button, Form, Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

// Inline CSS styles
const containerStyle = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  height: '100vh',
  backgroundColor: '#f7f7f7',
};

const formStyle = {
  maxWidth: '400px',
  width: '100%',
  padding: '20px',
  borderRadius: '8px',
  backgroundColor: '#fff',
  boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
};

const headingStyle = {
  textAlign: 'center',
  marginBottom: '20px',
  color: '#007bff', // Primary color used in Login
};

const buttonStyle = {
  backgroundColor: '#007bff', // Primary color used in Login
  borderColor: '#007bff',
};

export default function ForgotPassword() {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle password reset logic
    setMessage('A password reset link has been sent to your email address.');
  };

  return (
    <Container fluid
    className="d-flex justify-content-center align-items-center"
    style={{ 
      height: '100vh', 
      backgroundColor: '#e9f5e9',
      padding: '20px'
    }}>
      <div style={{
              maxWidth: '450px', 
              margin: '0 auto',
              backgroundColor: '#2c6e49',
              padding: '40px',
              borderRadius: '10px',
              boxShadow: '0 0 20px rgba(0, 0, 0, 0.15)',
              color: '#ffffff',
            }}>
        <h2 className="text-center mb-4" style={{ color: '#d4f4dd' }}>Forgot Password</h2>
        <Form onSubmit={handleSubmit}>
          <Form.Group controlId="formEmail">
            <Form.Label>Email address</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter your email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </Form.Group>
          <Button
            variant="primary"
            type="submit"
            className="w-100 mt-3"
            style={{
              backgroundColor: '#4caf50',
              borderColor: '#4caf50',
              padding: '12px',
              fontSize: '18px',
            }}
          >
            Send Reset Link
          </Button>
          {message && <div className="mt-3 text-center text-success">{message}</div>}
        </Form>
      </div>
    </Container>
  );
}
